body {
  margin: 0;
}

.wrapper {
  background-color: black;
  color: white;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  width: 70vw;
  padding: 0.5rem 1rem;
}
img {
  width: 100vw;
}

.slidebar {
  height: 0.8vh;
  display: flex;
  justify-content: space-between;
  width: 100%;
  div {
    background-color: rgb(218, 227, 235);
    width: 30%;
  }
}
h2 {
  display: inline-block;
  padding: 4px 6px;
  line-height: 1.4;
  font-size: 28px;
  font-weight: 700;
  color: aliceblue;
  background-color: black;
  border-radius: 4px;
  transform: rotate(355deg);
}

h3 {
  display: inline-block;
  padding: 4px 6px;
  font-weight: 700;
  color: rgb(122, 42, 136);
  background-color: rgb(255, 255, 103);
  border-radius: 4px;
  transform: rotate(15deg);
}

.bottomContainer {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 81vh;
  font-size: smaller;
}
