.wrapper {
  margin: 0;
  block-size: 100%;
  inline-size: 100%;
  height: 80vh;
  width: 92vw;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.header {
  font-family: "Courier New", Courier, monospace;
  display: flex;
  justify-content: space-between;
  height: 20vh;
  align-items: center;
  font-size: 2.5rem;
}
.icons {
  font-size: 1.5rem;
  display: flex;
  justify-content: space-around;
  width: 15vw;
  align-items: center;
}

.overflowX {
  overflow-x: auto;
  overscroll-behavior-x: contain;
  border: 1px solid hsl(0 0% 80%);
  border-radius: 1ex;
  padding: 2rem;
}

.horizontalFriendsList {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 10ch;
  gap: 2rem;

  & > figure {
    display: grid;
    gap: 1ex;
    margin: 0;
    text-align: center;
    position: relative;
    cursor: pointer;
    user-select: none;
    transition: transform 0.2s ease-in-out;

    &:hover {
      transform: scale(1.1);
    }

    &:last-child::after {
      content: "";
      position: absolute;
      width: 2rem;
      height: 100%;
      right: -2rem;
      inline-size: 2rem;
      block-size: 100%;
    }

    & > picture {
      display: inline-block;
      inline-size: 10ch;
      block-size: 10ch;
      border-radius: 50%;

      background: radial-gradient(hsl(0 0% 0% / 15%) 60%, transparent 0),
        radial-gradient(white 65%, transparent 0),
        linear-gradient(to top right, orange, deeppink);

      & > img {
        display: block;
        inline-size: 100%;
        block-size: 100%;
        object-fit: cover;
        clip-path: circle(42%);
      }
    }

    & > figcaption {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-weight: 500;
      a {
        text-decoration: none;
        color: black;
      }
    }
  }
}
